// import uniStatusBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue'
import mainBtn from '@/components/common/main-btn.vue'
import otherLogin from '@/components/common/other-login.vue'

export default {
	components:{
		mainBtn,otherLogin
	},
	data() {
		return {
			status:true,
			username:"",
			password:"TZH0810",
			phone:"17628197245",
			code:"",
			codeTime:0,
		}
	},
	onLoad(){
		
	},
	methods: {
		back(){
			uni.navigateBack({
				delta:1
			})
		},//初始化表单
		initForm(){
			this.username = this.password = this.phone = this.code = ''
		},
		//切换登录方式
		changeStatus(){
			this.initForm()
			this.status=!this.status
		},//获取验证码
		getCode(){
			if(this.codeTime>0){
				return;
			}
			if(!this.validate())return;
			this.codeTime = 60
			var timer=''
			 timer = setInterval(()=>{
				// clearInterval(timer)
				if(this.codeTime>=1){
					this.codeTime--
				}else{
					this.codeTime=0
					clearInterval(timer)
				}
			},1000)
			
			
		},
		//表单验证
		validate(){
			if(!(/^1[3456789]\d{9}$/.test(this.username))){ 
				uni.showToast({
					title:'无效的手机号',
					'icon':'none'
				})
				return false; 
			}
			 return true
		},
		//注册页
		openRegister(){
		
			uni.navigateTo({
				url: '/pages/register/register'
			});
		},
		//提交
		submit(){
			
			// console.log('tr')
			if(!this.validate())return;
			uni.request({
			    url: `${this.$C.webUrl}/authorizations`, 
				method:'POST',
			    data: {
			        username:this.username,
					password:this.password
			    },
			    success: (res) => {
					// console.log(res.data)
			        if(res.data.access_token){
						//缓存token方便授权认证
						// console.log(res.data)
						 uni.setStorageSync('access_token', res.data.access_token);
						 uni.reLaunch({
						     url: '/pages/test/test'
						 });
					}
			
			    }
			});
		}
	},
	computed:{
		disabled(){
			if((this.username === '' || this.password === '') && (this.phone ==='' || this.code === ''))return true;
			return false
		}
	}
}
